const utils = require('./utils.js');

function buildRelativeDateString(dateDiff) {
  var result = [];
  if (dateDiff.years) {
    result.push(dateDiff.years + 'y');
  }
  if (dateDiff.months) {
    result.push(dateDiff.months + 'm');
  }
  if (dateDiff.days) {
    result.push(dateDiff.days + 'd');
  }
  if (dateDiff.hours) {
    result.push(dateDiff.hours + 'h');
  }
  if (dateDiff.minutes) {
    result.push(dateDiff.minutes + 'm');
  }
  var dateString = result.join(" ");
  if (dateDiff.firstDateWasLater) {
    dateString = 'in ' + dateString;
  } else {
    dateString = dateString + ' ago';
  }
  return dateString;
}

$(document).ready(function () {
  var storeItems;
  var lastDate;

  updateTimezone();
  $.getJSON('/db/misc/store.json', function(data){
    storeItems = data;
    lastDate = moment();
    var currentDateItems = getStoreItemsPerDate(storeItems, lastDate.format("YYYY-MM-DD"), lastDate.format("YYYY-MM-DD"));
    updateUI(currentDateItems, lastDate);
  })
  .fail(function() {
    alert("error");
  });


  $('#incrementDate').on('click', function() {
    if (moment(lastDate).add(1, 'day') > moment().add(1, 'week')) {
      return false;
    }
    lastDate.add(1, 'day');
    var currentDateItems = getStoreItemsPerDate(storeItems, lastDate.format("YYYY-MM-DD"), lastDate.format("YYYY-MM-DD"));
    updateUI(currentDateItems, lastDate);
  });

  $('#decrementDate').on('click', function() {
    if (moment(lastDate).subtract(1, 'day') <= moment().subtract(1, 'day')) {
      return false;
    }
    lastDate.subtract(1, 'day');
    var currentDateItems = getStoreItemsPerDate(storeItems, lastDate.format("YYYY-MM-DD"), lastDate.format("YYYY-MM-DD"));
    updateUI(currentDateItems, lastDate);
  });

  function updateTimezone() {
    $('.timezone').text(moment().format("Z"));
  }

  function getStoreItemAvailability(storeItem) {
    var storeItemStart = moment.utc(storeItem.Start);
    var storeItemEnd = moment.utc(storeItem.End);
    var now = moment.utc();
    if (storeItemStart <= now && storeItemEnd >= now) {
      return "Current";
    } else if (storeItemStart > now) {
      return "Later";
    } else {
      return "Expired";
    }
  }

  function getStoreItemsPerDate(storeItems, fromDate, toDate) {
    var items = [];
    fromDate = moment(fromDate).milliseconds(0).seconds(0).minutes(0).hours(0);
    toDate = moment(toDate).milliseconds(0).seconds(59).minutes(59).hours(23);
    if (storeItems && storeItems.length > 0) {
      storeItems.forEach(function(storeItem){
        var storeItemStart = moment.utc(storeItem.Start);
        var storeItemEnd = moment.utc(storeItem.End);
        if (storeItemEnd >= fromDate && storeItemStart <= toDate) {
          storeItem.Availability = getStoreItemAvailability(storeItem);
          items.push(storeItem);
        }
      });
    }

    if (items && items.length > 0) {
      return items;
    }

    return false;
  }

  function getStoreItemUIRow(storeItemData) {
    var uiRow = $('<li/>', {'style': 'margin-bottom:35px;'})
    .append(
      $('<h4/>', {'text': storeItemData.Name})
    )
    .append(
      $('<p/>', {'text': storeItemData.Description})
    )
    .append(
      $('<p/>')
      .append(
        $('<span/>', {'html': '<strong>Cost:</strong> ' + storeItemData.Cost.Amount + ' ' + storeItemData.Cost.Type + '<br>'})
      )
      .append(
        $('<time/>', {'html': '<strong>Starts:</strong> <time class="relative" datetime="' + moment.utc(storeItemData.Start).format() + '">' + moment(storeItemData.Start).format("dddd, YYYY-MM-DD, h:mm a Z") + '</time><br>'})
      )
      .append(
        $('<span/>', {'html': '<strong>Expires:</strong> <time class="relative" datetime="' + moment.utc(storeItemData.End).format() + '">' + moment(storeItemData.End).format("dddd, YYYY-MM-DD, h:mm a Z") + '</time><br>'})
      )
      .append(
        $('<span/>', {'html': '<strong>Limit:</strong> ' + storeItemData.Limit})
      )
    )
    return uiRow;
  }

  function updateUI(storeItemsData, currentDate) {
    currentDate = currentDate.format("YYYY-MM-DD");
    $('input[name="current-date"]').val(currentDate);
    $('.current-items ul').empty();
    $('.upcoming-items ul').empty();
    $('.expired-items ul').empty();
    if (storeItemsData && storeItemsData.length > 0) {
      storeItemsData.forEach(function(storeItemData){
        var uiRow = getStoreItemUIRow(storeItemData);
        switch (storeItemData.Availability) {
          case "Current":
            $('.current-items ul').append(uiRow);
            break;
          case "Later":
            $('.upcoming-items ul').append(uiRow);
            break;
          case "Expired":
            $('.expired-items ul').append(uiRow);
            break;
        }
      });
    } else {
      var uiRow = $('<li/>', {'text': 'No items found'});
      $('.store-items').append(uiRow);
    }

    $('time.relative').each(function(){
      var publishedDatetime = moment($(this).attr('datetime'));
      var currentDatetime = moment();
      var dateDiff = moment(publishedDatetime).preciseDiff(currentDatetime, true);
      var dateString = buildRelativeDateString(dateDiff);
      $(this).text(dateString);
    });
  }

});



